'use strict';

(function () {

    document.addEventListener('DOMContentLoaded', function () {

        var imageSearch = require("js/image-search");
        var bodyTmpl = require('views/body.pug');
        var exampleTmpl = require('views/example.pug');
        var hostName = window.location.protocol + "//" + window.location.hostname + (window.location.hostname === "localhost" ? ":" + window.location.port : '');

        document.body.innerHTML = bodyTmpl() + exampleTmpl({ host: hostName });
        imageSearch.init(hostName);
    });
})();
