'use strict';

(function () {

    module.exports = {

        props: {
            images: [],
            apiPath: '',
            searchField: null,
            offsetField: null,
            searchButton: null,
            resultsTable: null,
            recentsTable: null
        },

        init: function init(apiPath) {

            this.props.apiPath = apiPath;
            this.props.searchField = document.getElementById('search-input');
            this.props.offsetField = document.getElementById('offset-input');
            this.props.searchButton = document.getElementById('search-button');
            this.props.resultsTable = document.getElementById('results-table');
            this.props.recentsTable = document.getElementById('recents-table');
            this.bindEvents();
            this.render();
        },

        bindEvents: function bindEvents() {
            var _this = this;

            this.props.searchButton.onclick = function () {
                var term = _this.props.searchField.value;
                var offsetVal = _this.props.offsetField.value;
                var offset = offsetVal ? offsetVal : 0;

                _this.getImages(term, offset);
            };

            document.onclick = function (e) {
                if (e && e.target && e.target.classList && e.target.classList.contains("recent-search")) {
                    _this.getImages(e.target.text, 0);
                }
            };
        },

        render: function render() {
            var _this2 = this;

            this.props.recentsTable.innerHTML = '<tr class="loader-container"><td><div class="loader"></div></td></tr>';
            this.getData(this.props.apiPath + "/api/recent", function (data) {
                if (data.length > 0) {
                    _this2.props.recentsTable.innerHTML = data.reduce(function (acc, val) {
                        return acc += '\
                            <tr>\
                                <td><a class="recent-search" href="javascript:void(0)">' + val.search + '</a></td>\
                            </tr>\
                        ';
                    }, '');
                }
            });

            if (this.props.images.length > 0) {
                this.props.resultsTable.innerHTML = this.props.images.reduce(function (acc, val) {
                    return acc += '\
                        <tr>\
                            <td><img src="' + val.thumbnailUrl + '" /></td>\
                            <td><a target="_blank" href="' + val.imageUrl + '">' + val.name + '</a></td>\
                        </tr>\
                    ';
                }, '');
            }
        },

        getData: function getData(url, callback) {
            var xhttp = new XMLHttpRequest();

            xhttp.onreadystatechange = function () {
                if (this.readyState === 4 && this.status === 200) {
                    callback(JSON.parse(xhttp.response));
                }
            };
            xhttp.open('GET', url, true);
            xhttp.send();
        },

        getImages: function getImages(term, offset) {
            var _this3 = this;

            this.props.resultsTable.innerHTML = '<tr class="loader-container"><td><div class="loader"></div></td></tr>';
            this.getData(this.props.apiPath + "/api/search/" + term + "?offset=" + offset, function (data) {
                _this3.props.images = data;
                _this3.render();
            });
        }

    };
})();
